import LazyLoad from 'react-lazyload';
import { CSSTransition } from "react-transition-group";

class HoverPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      videoLoaded: false,
      vWid: 0,
      vHei: 0
    }
    this.setVideoDims = this.setVideoDims.bind(this)
    this.onResize = this.onResize.bind(this)
    this.videoRef = React.createRef();
  }

  componentDidMount() {
    window.addEventListener("resize", this.onResize)
  }

  componentWillUnmount() {
    //window.removeEventListener("resize", this.onResize)
  }

  onResize() {
    if (this.videoRef.current != null) {
      let wid = this.videoRef.current.clientWidth;
      let hei = this.videoRef.current.clientHeight;
      this.setState({
        vWid: wid, vHei: hei
      })
    }
  }

  setVideoDims(e) {
    let wid = e.target.clientWidth;
    let hei = e.target.clientHeight;
    this.setState({ videoLoaded: true, vWid: wid, vHei: hei })
  }

  render() {
    return (
      <Box transition="transform 1s"
        transformStyle="preserve-3d"
        className="homepage-card-container"
        hoverTransform={this.props.hoverTransform}
      >
        <Row
          position="relative"
          width="100%"
          alignItems="center"
          key="h3"
          overflow="hidden"
          justifyContent="flex-end"
          backgroundColor="#CDF5FB"
          borderRadius="4%"
          boxShadow={this.props.shadow}
        >
          <Box width="100%" position="absolute" bottom="0px">
            <LazyLoad placeholder={<img style={{ width: "100%" }} src={this.props.thumb} />}>
              <video
                muted
                playsInline
                width="100%"
                autoPlay
                loop
                poster={this.props.thumb}
                onLoadedData={this.setVideoDims}
                ref={this.videoRef}
              >
                <source src={this.props.videoSrc} type="video/mp4" />
              </video>
            </LazyLoad>
          </Box>
          <Box position="relative" props={{ src: this.props.frame }} component="img" width="100%" zIndex="1"></Box>
        </Row>
        <Box
          width="80%"
          color="black"
          position="absolute"
          top="0px"
          left="0px"
          minWidth="200px"
          transform="translate3d(0px, 0px, -10px) rotateZ(0deg)"
          backgroundColor={this.props.cardColor}
          borderRadius="8px"
          padding="24px"
          fontSize="12px"
          overflow="none"
          fontWeight="600"
          max-width="300px"
          className="card-description"
          transition="transform 1s"
          transformStyle="preserve-3d"
          fontFamily="Open Sans"
          opacity="0"
        >
          {this.props.description}
        </Box>
      </Box>
    )
  }
}

